.chat-container {
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: #f5f5f5;
  border-radius: 8px;
  overflow: hidden;
}

.chat-messages {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.message {
  max-width: 70%;
  padding: 10px 15px;
  border-radius: 18px;
  word-break: break-word;
  line-height: 1.4;
}

.message-user {
  align-self: flex-end;
  background-color: #0084ff;
  color: white;
  border-radius: 18px 18px 4px 18px;
}

.message-bot {
  align-self: flex-start;
  background-color: #e5e5ea;
  color: #000;
  border-radius: 18px 18px 18px 4px;
}

.message-meta {
  font-size: 0.8em;
  opacity: 0.7;
  margin-top: 5px;
}

.source {
  font-style: italic;
  font-size: 0.8em;
  margin-top: 4px;
}

.chat-input-container {
  display: flex;
  padding: 15px;
  background-color: #fff;
  border-top: 1px solid #e0e0e0;
}

.chat-input {
  flex: 1;
  padding: 10px 15px;
  border: 1px solid #ddd;
  border-radius: 20px;
  outline: none;
  font-size: 14px;
}

.send-button {
  margin-left: 10px;
  padding: 0 15px;
  background-color: #0084ff;
  color: white;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.send-button:hover {
  background-color: #0066cc;
}

.send-button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.typing-indicator {
  display: flex;
  align-items: center;
  margin-top: 5px;
  align-self: flex-start;
  color: #666;
  font-style: italic;
}

.welcome-message {
  text-align: center;
  margin: 20px 0;
  color: #666;
}

.no-bot-message {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  color: #666;
  text-align: center;
  padding: 20px;
}

.sources-list {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
  margin-top: 8px;
}

.source-tag {
  background-color: #eee;
  padding: 3px 8px;
  border-radius: 12px;
  font-size: 0.8em;
  color: #666;
} 